@import "./../../fonts/font.css";

.Stake{
  width: 100%;
 display: flex;
  flex-direction: column;
  align-items: center;
   color: #FEF1EC;
      box-sizing: border-box;
  :global{
     .text_assets{
       width: 311px;
       height: 36px;
       margin-bottom: 40px;
       display: flex;
       .col{
        width: 4px;
        height: 32px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
         span{
          // display: inline-block;
           width: 4px;
           height: 8px;
           //background-color: rgb(148, 194, 60);
           background-color: rgb(108 149 30);
           margin-right: 2px;
           border-radius: 10px;
         }
       }
       .text{
         color: #FEF1EC;
         font-size: 24px;
         padding-left: 28px;
       }
       .text_opt{
         color: rgb(148, 194, 60);
       }
     }
     .btn{ 
      width: 343px;
      height: 38px;
      display: flex;
      font-size: 16px;
      box-sizing: border-box;
      justify-content: space-between;
      margin-bottom: 20px;
      .btn_1{
      font-weight: 500;
        width: 162px;
        padding: 0 8px;
      box-sizing: border-box;
      border: 1px solid transparent;
       --tw-border-opacity: 1;
      border-radius: 18px;
      line-height: 38px;
      border-color: rgb(148, 194, 60);
      }
      .active_btn{
        //background-color: rgb(148, 194, 60);
        background-color: rgb(108 149 30);
      }
      .active_btn2{
        background-color: rgb(96, 178, 246);
      }
     }
     .supplyBase{
      width: 343px;
      height: 153px;
      padding: 20px 28px;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);
      box-sizing: border-box;
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-bottom: 20px;
      .item{
        width: 287px;
        height: 24px;
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        font-weight: 100;
      }
      .item_2{
        color:#FEF1EC;
        font-size: 20px;
        font-weight: 400;
      }
      .item_3{
        font-size: 18px;
        font-weight: 200;

      }
     }
     .ratio{
      @extend .supplyBase;
      justify-content:flex-start;
      height: 230px;
      .item_2{
        height: 54px;
        color: #000000;
        padding:16px 0 0 ;
         .info{
          width: 22px;
          height: 24px;
          margin-left: 5px;
         }
         :nth-child(1){
          text-align: left;
         }
         :nth-child(2){
          display: flex;
          white-space: nowrap;
         }
      }
      .item_3{
       height: 96px;
       padding: 16px 0 0;
       display: flex;
       flex-direction: column;
       font-size: 14px;
       span{
        text-align: left;
       }
      }
     }
     .claimable{
      @extend .supplyBase;
      justify-content: start;
      width: 343px;
      height: 320px;
      .claim{
       
        display: flex;
        justify-content: space-between;
        padding: 20px 0 0;
        align-items: center;
        .text{
          font-size: 20px;
          color: #FEF1EC;
       font-weight: 400;
        }
      
      }
        .btn_claim{
          width: 112px;
          height: 44px;
          font-size: 16px;
          // opacity: .7; 
          border: 1px solid transparent;
            --tw-border-opacity: 1;
           border-radius: 10px;
       border-color: rgb(96, 178, 246);
       line-height: 44px;
       text-align: center;
       font-weight: 400;
        }
      .claim_all{
        @extend  .btn_claim;
        width: 287px;
      }
     }
     .connect_{
      @extend  .supplyBase;
      height: 306px;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);
      padding: 20px 26px;
      .connect_text{
        padding: 64px 0;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        .text_1{
          font-weight: 300;
          height: 96px;
          padding: 0 0 48px;
          font-size: 16px;
          text-align: left;
        }
        .wallet_btn{
            border: 1px solid transparent;
            --tw-border-opacity: 1;
           border-radius: 10px;
       border-color: rgb(96, 178, 246);
       text-align: center;
       font-weight: 400;
       font-size: 16px;
       padding: 8px 16px;
        }
      }
     }
  }
}