@import "./../../fonts/font.css";

.Sonne{
  width: 343px;
  height: 624px;
  display: flex;
  flex-direction: column;
  margin:112px 0 64px ;
  padding: 0 16px ;
  box-sizing: border-box;
  font-size: 18px;
  color: #FFFFFF;
  :global{
     .logo{
      //width: 90px;
       width: 34px;
      height: 34px;
      padding: 0 0 24px;
     }
     .text-footer{
      text-align: left;
      font-weight: 200;
     }
     .full_button{
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      text-align: left;
      font-weight: 300;
       .item_title{
        margin-top: 24px;
        width: 136px;
           display: flex;
          flex-direction: column;
          color: rgb(148, 194, 60);
          padding: 0 0 24px;
          font-size: 16px;
       }
       .item_text{
        font-size: 16px;

       }
     }
  }
}