@import "./../../fonts/font.css";

.Lending{
  width: 100%;
  height: 632px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  :global{
    .text-neutral{
      --tw-text-opacity: 1;
          color: rgb(254 241 236/var(--tw-text-opacity));
     }
   .content{
     width: 343px;
     height: 100%;
     display: flex;
     flex-direction: column;
     overflow: hidden;
     align-items: center;
     .title{
      width: 100%;
      height: 36px;
      display: flex;
      justify-content: space-between;
      font-size: 20px;
      margin-bottom: 40px;
       .title_text{
        width: 50%;
         padding-bottom: 16px;
         color: #FEF1EC;
         font-weight: 200;
        //  transition: all 1s step-start;
       }
       .active{
         font-weight: 300;
         color: rgb(148, 194, 60);
         border-bottom: 2px solid rgb(148, 194, 60);
       }
     }
     .text_assets{
       width: 311px;
       height: 36px;
       margin-bottom: 40px;
       display: flex;
       .col{
        width: 4px;
        height: 32px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
         span{
          // display: inline-block;
           width: 4px;
           height: 8px;
           //background-color: rgb(148, 194, 60);
           background-color: rgb(108 149 30);
           margin-right: 2px;
           border-radius: 10px;
         }
       }
       .text{
         color: #FEF1EC;
         font-size: 24px;
         padding-left: 28px;
       }
     }
     .text_list{
      width: 311px;
      height: 406px;
      padding: 16px 28px ;
      font-size: 16px;
      box-sizing: border-box;
      border-radius: 9.375px;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);
      // display: flex;
      overflow: hidden ;
     
      .search{
        height: 64px;
        width: 255px;
        display: flex;
        position: relative;
        .search_text{
          width: 255px;
          height: 32px;
          border-radius: 9999px;
          --tw-bg-opacity: 1;
          background-color: rgb(20 6 3/var(--tw-bg-opacity));
          display: flex;
          align-items: center;
          .search_icon{
            width: 13.17px;
            height: 13.17px;
            margin: 0 16px;
          }
          input{
            width: 209px;
            height: 28px;
            background-color: transparent;
            border: transparent;
            padding: 0;
            border-width:0;
            @extend .text-neutral;
            font-size: 21px;
            font-family: inherit;
            font-weight: inherit;
          }
          input:focus{
             outline: none;
           }
        }
        .cancel{
          position: absolute;
          top: 8px;
          left: 90%;
          width: 18px;
          height: 18px;
        }
      }
      .table_title{
        height: 100%;
        scrollbar-width: none;
        -ms-overflow-style: none;
         overflow:scroll ;
         margin-right: 16px;
      }
     .table_title::-webkit-scrollbar {
    display: none;
}
      th{
        // width: 311px;
        font-size: 12px;
       color: #FEF1EC;
       height: 64px;
       padding: 16px 32px 16px 12px;
       text-align: left;
       box-sizing: border-box;
       white-space:nowrap;
      }
      .img_logo{
        width: 24px;
        height: 24px;
        margin-right: 10px;
        border-radius: 24px;
       }
       .td_1{
        display: flex;
        font-size: 19px;
        font-weight: 300;
        color: #FEF1EC;
        padding: 8px 32px 8px 8px;
        
       }
       td{
         color: #FEF1EC;
       }
       .sonne_icon{
        width: 16px;
        height: 16px;
       }
       .sonne_text{
        color: rgb(96, 178, 246);
       }
     }
   }
  }
}
