@import "./../../fonts/font.css";


.Model{
  width: 100%;
  height: 100vh;
   position: absolute;
   top: 64px;
   left: auto;
    display: flex;
   justify-content: center;
   z-index: 99;
   background-color: rgba(0,0,0,0.5);
  :global{
    .text-light {
     //color: #FEF1EC;
      color: #2d2c2b;
    } 
    .model_content{
      //width: 100%;
      width: 343px;
      padding: 0 16px;
      height: 389px;
      display: flex;
     --tw-bg-opacity:.8;
     background-color: rgb(148, 194, 60);
      //background-color: rgb(148 194 60/var(--tw-bg-opacity));
      flex-direction: column;
      .item_1{
        //width: 343px;
        height: 64px;
        padding: 0 8px 0;
        font-size: 20px;
        //color: #FEF1EC;
        color: #295698!important;
        text-align: right;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: end;
        border-bottom:2px solid transparent;
        --tw-border-opacity: 1;
        border-bottom-color: #295698!important;
        .sonne-icon{
          width: 28px;
          height: 28px;
          margin-right: 5px;
          border-radius: 28px;
        }
      }
      .item{
        @extend  .text-light;
        font-weight: 500;
        text-align: left;
        height: 44px;
        line-height: 44px;
        font-size: 20px;
        padding: 8px  12px;
      }
      .active_item{
        --tw-text-opacity: 1;
       //color: rgb(96, 178, 246);
        color: #295698!important;
      }
    }
  }
}