
.fundraising{
      color: #FEF1EC;
   display: flex;
    flex-direction: column;
    align-items: center;
   :global{
      .text_assets{
       width: 311px;
       height: 36px;
       margin-bottom: 40px;
       display: flex;
       align-items: center;
       .col{
        width: 4px;
        height: 32px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
         span{
          // display: inline-block;
           width: 4px;
           height: 8px;
           //background-color: rgb(148, 194, 60);
           background-color: rgb(108 149 30);
           margin-right: 2px;
           border-radius: 10px;
         }
       }
       .text{
         color: #FEF1EC;
         font-size: 24px;
         padding-left: 28px;
         text-align: left;
       }
       .text_opt{
         color: rgb(148, 194, 60);
       }
     }
     .card{
      margin-top: 10px;
      width: 256px;
      height: 100px;
       border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 48px;
      --tw-bg-opacity: 1; 
      padding: 16px;
      box-sizing: border-box;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);
    .text_1{
      font-size: 18px;
      color: rgb(170 213 248);
      padding: 0 0 16px;
    }
    .text_2{
      font-size: 16px;
      color: #FEF1EC;
      display: flex;
      align-items: center;
    }
     }
     .USDC_logo{
      width: 16px;
      height: 16px;
      margin-left: 8px;
     }
     .card_2{
      @extend .card;
      width: 343px;
      height: 80px;
      margin: 0 0 48px;
      padding: 16px;
      font-size: 16px;
      .onne-icon{
        display: inline-block;
        width: 16px;
        height: 16px;
        margin-right: 4px;
      }
      
     }
     .card_3{
      @extend .card_2;
        height: 397px;
        padding: 24px 0;
        box-sizing: border-box;
        .usdc{
          width: 343px;
          height: 113px;
          border-bottom: 2px solid rgb(148, 194, 60);
          display: flex;
            font-size: 16px;
                font-weight: 200;
          .dep_usdc{
            width: 163px;
            font-size: 21px;
            color: #FEF1EC;
            padding: 32px 0 16px 24px;
            text-align: left;
                font-weight: 400;
          }
          .dep_usdc_2{
            width: 178px;
            height: 113px;
            padding: 32px 24px 16px 0;
            display: flex;
            flex-direction: column;
            align-items: start;
            box-sizing: border-box;
            font-size: 18px;
            span{
              height: 30px;
            }
          }
        }
         .color_{
           color: rgb(170 213 248);
          }
        .balance{
          width: 295px;
          height: 24px;
          margin: 24px 0;
          text-align: right;
        }
        .usdc_box{
          height: 60px;
          width: 295px;
          background-color: #140603;
          margin: 16px 0;
          padding: 8px;
            box-sizing: border-box;
            font-size: 20px;
            border-radius: 10px;
          display: flex;
          align-items: center;
        }
         input{
            width: 208px;
            height: 44px;
            background-color: transparent;
            border: transparent;
            padding: 0;
            border-width:0;
            // padding: 8px 8px 8px 12px;
            color: #FEF1EC;
            font-size: 20px;
            box-sizing: border-box;
            opacity: .5;
          }
          input:focus{
             outline: none;
           }
      .btn{
        width: 295px;
        height: 76px;
        margin: 32px 24px 0;
        border: 1px solid rgb(148, 194, 60);
        box-sizing: border-box;
        border-radius: 10px;
        opacity: .5;
      }
     }
   }
}