@import "./../../fonts/font.css";

.Market{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  :global{
    .text_assets{
       width: 311px;
       height: 36px;
       margin-bottom: 40px;
       display: flex;
       .col{
        width: 4px;
        height: 32px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
         span{
          // display: inline-block;
           width: 4px;
           height: 8px;
           //background-color: rgb(148, 194, 60);
           background-color: rgb(108 149 30);
           margin-right: 2px;
           border-radius: 10px;
         }
       }
       .text{
         color: #FEF1EC;
         font-size: 24px;
         padding-left: 28px;
       }
       .text_opt{
         color: rgb(148, 194, 60);
       }
     }
     .total{
      width: 311px;
      height: 72px;
         color: #FEF1EC;
         display: flex;
         flex-direction: column;
         align-items: flex-start;

          .font-light{
          font-size: 16px;
           padding: 0 0 12px;
         }
         .text-3xl{
          font-size: 30px;
          line-height: 36px;
          height: 36px;
         }
     }
        
     .w_full{
      @extend .total;
      width: 343px;
      height: 276px;
      margin: 8px 0 0 ;
      padding: 24px  32px;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);
      box-sizing: border-box;
      border-radius: 15px;
      margin-bottom: 16px;
      .uppercase{
        margin-top: 25px;
        font-size: 12px;
        padding: 0 0 16px;
      }
      .item{
        display: flex;
        align-items: center;
        margin-bottom: 16px;
         .item_icon{
          width: 24px;
          height: 24px;
          margin: 0 12px 0 0 ;
        }
        .text{
          width: 44px;
          font-size: 16px;
          text-align: left;
        }
        .text_title{
          width: 80px;
          display: flex;
          justify-content: space-between;
        }
      }
      .progress_bar{
        width: 114px;
        height: 8px;
        background-color: #140603;
        margin: 8px 16px;
        border-radius: 10px;
        position: relative;
        .rate{
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
        border-radius: 10px;
           --tw-bg-opacity: 1;
         background-color: rgb(96, 178, 246);
        }
      }
     
     }
     .w_full_2{
      --tw-bg-opacity: 1;
       //background-color: rgb(148, 194, 60);
       background-color: rgb(108 149 30);

     }
      .text_list{
      width: 311px;
      height: 406px;
      padding: 16px 28px ;
      font-size: 16px;
      color: #000;
      box-sizing: border-box;
      border-radius: 9.375px;
        //background-color: rgb(148, 194, 60);
        background-color: rgb(108 149 30);
      // display: flex;
      overflow: hidden ;
      // flex-direction: column;
      // align-items: center;
      .search{
        height: 64px;
        width: 255px;
        display: flex;
        position: relative;
        .search_text{
          width: 255px;
          height: 32px;
          border-radius: 9999px;
          --tw-bg-opacity: 1;
          background-color: rgb(20 6 3/var(--tw-bg-opacity));
          display: flex;
          align-items: center;
          .search_icon{
            width: 13.17px;
            height: 13.17px;
            margin: 0 16px;
          }
          input{
            width: 209px;
            height: 28px;
            background-color: transparent;
            border: transparent;
            padding: 0;
            border-width:0;
            font-size: 21px;
            color: #FEF1EC;
            font-family: inherit;
            font-weight: inherit;
          }
          input:focus{
             outline: none;
           }
        }
        .cancel{
          position: absolute;
          top: 8px;
          left: 90%;
          width: 18px;
          height: 18px;
        }
      }
      .table_title{
        height: 100%;
        scrollbar-width: none;
        -ms-overflow-style: none;
         overflow:scroll ;
         margin-right: 16px;
      }
     .table_title::-webkit-scrollbar {
    display: none;
}
      th{
        // width: 311px;
        font-size: 12px;
       color: #FEF1EC;
       height: 64px;
       padding: 16px 32px 16px 12px;
       text-align: left;
       box-sizing: border-box;
       white-space:nowrap;
      }
      .img_logo{
        width: 24px;
        height: 24px;
        margin-right: 10px;
       }
       .td_1{
        display: flex;
        font-size: 19px;
        font-weight: 300;
        color: #FEF1EC;
        padding: 8px 32px 8px 8px;
        
       }
       td{
         color: #FEF1EC;
       }
       .sonne_icon{
        width: 16px;
        height: 16px;
       }
       .sonne_text{
        color: rgb(148, 194, 60);
       }
     }
  }
}