@import "./../../fonts/font.css";
.Claim{

  :global{
      .text_assets{
       width: 311px;
       height: 36px;
       margin-bottom: 40px;
       display: flex;
       align-items: center;
       .col{
        width: 4px;
        height: 32px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
         span{
          // display: inline-block;
           width: 4px;
           height: 8px;
           background-color: rgb(148, 194, 60);
           margin-right: 2px;
           border-radius: 10px;
         }
       }
       .text{
         color: #FEF1EC;
         font-size: 24px;
         padding-left: 28px;
         text-align: left;
       }
       .text_opt{
         color: rgb(148, 194, 60);
       }
     }
     .card{
      width: 343px;
      height: 484px;
    //background-color: rgb(148, 194, 60);
    background-color: rgb(108 149 30);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
     .sonne_logo_2{
       width: 72px;
       height: 72px;
       margin: 32px 0;
       border-radius: 72px;
      }
      .items_center{
        display: flex;
    flex-direction: column;
    align-items: center;
      }
      .lge{
        font-size: 24px;
    color: rgb(96, 178, 246);
    margin: 0 0 32px;
      }
      .text-claim-header{
        color: #FEF1EC;
        margin: 0 0 16px;

      }
      .sonne_text{
        display: flex;
        align-items: center;
        font-size: 24px;
          color: #FEF1EC;
          margin-bottom: 28px;

      }
      .sonne-icon{
        width: 20px;
        height: 20px;
        margin: 0 4px 0 16px;
        border-radius: 20px;
      }
       
     }
     .wallet_btn{
      width: 312px;
      height: 44px;
            border: 1px solid transparent;
            --tw-border-opacity: 1;
           border-radius: 20px;
       border-color: rgb(96, 178, 246);
       text-align: center;
       font-weight: 400;
       font-size: 16px;
      line-height: 44px;
      color: #FEF1EC;
        }
  }
}