@import "./../../fonts/font.css";
.headers{
   width: 100%;
   height: 64px;
   --tw-bg-opacity:1;
   background-color: rgb(148, 194, 60);
   display: flex;
   justify-content: center;
   margin-bottom: 40px;
   position: relative;
   :global{
      .content{
     width: 343px;
     height: 100%;
     display: flex;
     justify-content: space-between;
     align-items: center;
     .right{
       height: 30px;
       //width: 96px;
       //height: 22.398px;
        .logo{
          //width: 84px;
          //height: 24.398px;
          height: 30px;
          border-radius: 30px;
        }
       }
       .left{
        @extend .content;
        width: 200px;
        height: 64px;
        .left_connect{
          @extend .text-neutral;
          width: 150px;
          height: 42px;
          --tw-border-opacity: 1;
          padding: 8px 16px;
          font-size: 16px;
          //box-sizing: border-box;
          //display: flex;
          //align-items: center;
          border: 1px solid rgb(20 75 121);
          color: #295698!important;
          background-color: rgb(148, 194, 60);
          border-radius: 1rem;
          cursor: pointer;
         }
         .left_path{
           width: 24px;
           height: 24px;
           display: flex;
           flex-direction: column;
           justify-content: space-around;
           align-items: center;
           span{
             width: 18px;
             height: 2px;
              --tw-text-opacity: 1;
             background-color: #295698!important;
             cursor: pointer;
           }
         }
       }
       .cancel{
        width: 100%;
        display: flex;
        justify-content: flex-end;
       }
       .cancel_2{
        width: 32px;
        height: 32px;
        text-align: right;
         cursor: pointer;
       }
       
     }
    //  白色字体
     .text-neutral{
      --tw-text-opacity: 1;
     color: rgb(254 241 236/var(--tw-text-opacity));
     }
    //  黄色字体
     .text-light{
      --tw-text-opacity: 1;
      color: rgb(253 166 105/var(--tw-text-opacity));
     }
   }
   
}